// SECTION television :: START

.television {
  padding-top: 40px;
  padding-bottom: 90px;

  &__wrapper {
    z-index: 2;
    width: 100%;
    max-width: 1180px;
  }

  &__line {
    z-index: 0;
    position: absolute;
    top: 0;
    right: 41px;
    width: 2px;
    height: 303px;
    background-color: #FFFFFF;
  }

  &__title {
    padding-bottom: 44px;
    font-family: 'Aktiv Grotesk Ex', sans-serif;
    font-weight: 400;
    font-size: 30px;
    line-height: 38px;
    color: #FFFFFF;
  }

  &__box {
    &-wrapper {
      left: -20px;
      width: calc(100% + 20px);

      > div {
        width: 100%;
        max-width: calc(100% / 5);
        padding-left: 20px;
        padding-bottom: 25px;
      }
    }

    &-head {
      height: 505px;
      background-color: #000000;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        transform-origin: center;
        @include transition(transform 0.5s);
      }
    }
  }

  .card {
    border: none;
    border-radius: 0;
    background-color: transparent;

    &:hover {
      img {
        transform: scale(1.1);
        @include transition(transform 0.45s);
      }

      .card__more {
        .icon-font {
          transform: translateX(100%);
          @include transition(transform);
        }
      }
    }

    &__overlay {
      white-space: nowrap;
      z-index: 0;
      position: absolute;
      top: -20px;
      left: 15px;
      color: #000000;
      font-size: 64px;
      line-height: 1;
      transform-origin: 0 center;
      transform: rotate(90deg);
      font-family: 'Aktiv Grotesk Ex', sans-serif;
      text-shadow: 0 0 1px rgba(#fff, 0.5);
    }

    &__more {
      z-index: 2;
      position: absolute;
      left: 36px;
      bottom: 30px;
      display: flex;
      flex-direction: column;

      span {
        padding-bottom: 12px;
        font-family: 'Aktiv Grotesk Ex', sans-serif;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
      }

      .icon-font {
        font-size: 14px;
        color: #ffffff;
        @include transition(transform);
      }
    }

    &-body {
      padding: 15px 20px 15px 0;
    }

    &-title {
      margin-bottom: 5px;
      font-family: 'Aktiv Grotesk Ex', sans-serif;
      font-size: 16px;
      line-height: 20px;
      color: #FFFFFF;
    }

    &-text {
      font-weight: 300;
      font-size: 16px;
      line-height: 18px;
      color: #FFFFFF;
    }
  }
}

// SECTION television :: END